.post {
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.postTop {
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}

.postAvatar {
    margin-right: 10px;
}

.postTopInfo {}

.postTopInfo h3 {
    font-size: medium;
}

.postTopInfo p {
    font-size: small;
    color: grey;
}

.postBottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
}

.postImage img {
    width: 100%;
}

.postOptions {
    padding-top: 10px;
    border-top: 1px solid lightgrey;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: grey;
    cursor: pointer;
    padding: 15px;
}

.postOption {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1;
}

.postOption p {
    margin-left: 10px;
}

.postOption:hover {
    background-color: #eff2f5;
    border-radius: 10px;
}