.login {
    display: grid;
    place-items: center;
    height: 100vh;
}

.login div img {
    object-fit: contain;
    height: 150px;
}

.login div {
    display: flex;
    flex-direction: column;
}

.login button {
    width: 300px;
    background-color: #900C3F;
    color: #eff2f5;
    font-weight: 800;
}

.login button:hover {
    background-color: white;
    color: #900C3F;
}